<template>
  <div class="container" v-if="this.$root.UserRole == 'User'">
    <div class="row">
      <ItemList ref="nextEpisodeList"></ItemList>
    </div>
    <div class="row">
      <ItemList ref="savedSeriesList"></ItemList>
    </div>
    <div class="row">
      <ItemList ref="savedFilmList"></ItemList>
    </div>
  </div>
  <div class="container" v-else-if="this.$root.UserRole == 'Admin'">
    <div class="row">
      <ItemList ref="recommendedList"></ItemList>
    </div>
  </div>
</template>

<script>
  import ItemList from '../components/ItemList.vue'
  import axios from 'axios'

  export default {
    components: {
      ItemList
    },
    methods: {
      loadData() {
        var self = this
    if (this.$root.UserRole == 'User') {
      this.$refs.nextEpisodeList.Title = "Next Episodes to watch:"
      axios.get(this.$root.backendUrl + "/User/Next", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') } })
        .then(function (response) {
          for (var i = 0; i < response.data.length; i++) {
            self.$refs.nextEpisodeList.items.push(response.data[i])
          }
        })
        .catch(function (error) {
          console.log(error)
        })
      this.$refs.savedSeriesList.Title = "Saved Series for later:"
      axios.get(this.$root.backendUrl + "/User/Series/Saved", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') } })
        .then(function (response) {
          for (var i = 0; i < response.data.length; i++) {
            self.$refs.savedSeriesList.items.push(response.data[i])
          }
        })
        .catch(function (error) {
          console.log(error)
        })
      this.$refs.savedFilmList.Title = "Saved Films for later:"
      axios.get(this.$root.backendUrl + "/User/Films/Saved", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') } })
        .then(function (response) {
          for (var i = 0; i < response.data.length; i++) {
            self.$refs.savedFilmList.items.push(response.data[i])
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    } else if (this.$root.UserRole == "Admin") {
      this.$refs.recommendedList.Title = "Recommendations:"
      axios.get(this.$root.backendUrl + "/Series/Recommended")
        .then(function (response) {
          if (self.$refs.recommendedList.items == null) {
            self.$refs.recommendedList.items = []
          }
          for (var i = 0; i < response.data.length; i++) {
            self.$refs.recommendedList.items.push(response.data[i])
          }
        })
        .catch(function (error) {
          console.log(error)
        })
      axios.get(this.$root.backendUrl + "/Films/Recommended")
        .then(function (response) {
          if (self.$refs.recommendedList.items == null) {
            self.$refs.recommendedList.items = []
          }
          for (var i = 0; i < response.data.length; i++) {
            self.$refs.recommendedList.items.push(response.data[i])
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
      }
    },
    watch: {
      UserRole() {
        this.loadData();
      }
    },
    async mounted() {
      this.loadData();
    }
  }
</script>
