<template>
  <div class="commentSection">
      <h3>Comments:</h3>

      <div class="comment-list">
        <span class="spinner"></span>
        <div v-for="(comment, index) in Comments">
          <div class="slide">
            <div class="profile">
              <p class="name">
                {{ comment.author }} <span class="date">{{ comment.commentDate }}</span><a @click="removeComment(comment, index)" v-show="UserRole == 'Admin'" class="fas fa-times-circle p-1 m-1"></a>
              </p>
            </div>
            <div class="msg">
              <div class="msg-body">
                <p v-html="comment.text">{{ comment.text }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="comment-box" id="comment">
        <div class="loader">
          <span class="spinner"></span>
        </div>

        <p>Leave a comment ({{ this.$root.NickName }}):</p>
        <form @submit.prevent="">
          <textarea v-model="message" class="input-message" name="message" id="message" rows="3" placeholder="Your comment..." required></textarea>
          <button ref="sendComment" @click="updateComment(message)" disabled>Comment</button>
        </form>
      </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    data() {
      return {
        message: "",
        shownComments: null
      }
    },
    props: {
      comments: {
        type: Array,
        default: []
      }
    },
    computed: {
      UserRole() { return this.$root.UserRole; },
      Comments() {
        if (this.shownComments == null) { this.shownComments = this.comments == null ? [] : this.comments; }
        this.shownComments.sort(function (a, b) { return a.commentDate.localeCompare(b.commentDate);})
        return this.shownComments;
      }
    },
    methods: {
      updateComment(message) {
        var self = this;
        console.log(this.message);
        axios.put(this.$root.backendUrl + this.$route.path + '/Comment', {commentText: this.message}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            self.shownComments.push({
              commentID: response.data.commentID,
              author: response.data.author,
              commentDate: response.data.commentDate,
              text: response.data.text
            });
            self.message = "";
          })
          .catch(function (error) {
            alert(error)
          })
        
      },
      removeComment(comment, index) {
        var self = this;
        axios.delete(this.$root.backendUrl + this.$route.path + "/Comment/" + comment.commentID, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') } })
          .then(function (response) {
            self.shownComments.splice(index, 1);
          })
          .catch(function (error) {
            alert(error)
          });
      }
    },
    watch: {
      comments: function (value) { this.shownComments = this.comments; },
      message: function (NewMessage, oldMessage) {
        if (NewMessage != '') {
          this.$refs.sendComment.disabled = false;
        } else {
          this.$refs.sendComment.disabled = true;
        }
      }
    }

  }
</script>

