  .commentSection {
    width: 100%;
    margin-top: 15px;
    text-align: left;
  }
  .comment-box {
    width: 100%;
    margin-top: 15px;
    padding: 10px;
    background-color: #eeeeee;
  }
  .comment-list {
    width: 100%;
  }
  .input-message {
    width: 100%;
  }

  .slide {
    margin-top: 15px;
    width: 100%;
    text-align: left;
    background-color: #eeeeee;
    padding: 20px;
  }
  .profile {
    font-family: 'Times New Roman', Times, serif;
  }
