<template>
  <div class="container" v-else-if="this.$root.UserRole == 'user'">
    <div class="row">
      <ItemList ref="favoriteSeriesList"></ItemList>
    </div>
    <div class="row">
      <ItemList ref="favoriteFilmList"></ItemList>
    </div>
  </div>
</template>

<script>
  import ItemList from '../components/ItemList.vue'
  import axios from 'axios'

  export default {
    components: {
      ItemList
    },
    async mounted() {
      if (this.$root.UserRole == 'User') {
        var self = this
        this.$refs.favoriteSeriesList.Title = "Favorites Series:"
        axios.get(this.$root.backendUrl + "/User/Series/Favorite", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            for (var i = 0; i < response.data.length; i++) {
              self.$refs.favoriteSeriesList.items.push(response.data[i])
            }
          })
          .catch(function (error) {
            console.log(error)
          })
        this.$refs.favoriteFilmList.Title = "Favorites Films:"
        axios.get(this.$root.backendUrl + "/User/Films/Favorite", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            for (var i = 0; i < response.data.length; i++) {
              self.$refs.favoriteFilmList.items.push(response.data[i])
            }
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  }
</script>
