<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="secondary">

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav v-if="IsUser">
        <b-navbar-nav>
          <b-navbar-brand href="#/Home">WatchSushi</b-navbar-brand>
          <b-nav-item href="#/Favorites">Favorites</b-nav-item>
          <b-nav-item href="#/Recommendations">Recommendations</b-nav-item>
        </b-navbar-nav>
      </b-collapse>

      <b-collapse id="nav-collapse" is-nav v-if="IsAdmin">
        <b-navbar-nav>
          <b-navbar-brand href="#/Home">WatchSushi</b-navbar-brand>
        </b-navbar-nav>
      </b-collapse>

      <b-collapse id="nav-collapse" is-nav v-if="!IsAdmin && !IsUser">
        <b-navbar-nav>
          <b-navbar-brand href="/">WatchSushi</b-navbar-brand>
        </b-navbar-nav>
      </b-collapse>

      <b-collapse id="nav-collapse" is-nav>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto" v-if="!IsAdmin && !IsUser">

          <b-navbar-nav>
            <b-nav-item href="#/Signin">Sign in</b-nav-item>
            <b-nav-item href="#/Signup">Sign up</b-nav-item>
          </b-navbar-nav>
        </b-navbar-nav>

        <b-navbar-nav class="ml-auto" v-if="IsAdmin">

          <b-navbar-nav>
            <b-nav-form v-on:submit.prevent="">
              <b-form-input size="sm" class="mr-sm-2" ref="searchQuery" v-on:keyup.enter="search" placeholder="Search"></b-form-input>
              <b-button size="sm" class="my-2 my-sm-0 bg-info" type="button" @click="search">Search</b-button>
            </b-nav-form>

            <b-nav-item-dropdown right>
              <!-- Using 'button-content' slot -->
              <template v-slot:button-content>
                <em>{{ $root.UserName }}</em>
              </template>
              <b-dropdown-item @click="logout">Sign Out</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-navbar-nav>

        <b-navbar-nav class="ml-auto" v-if="IsUser">

          <b-navbar-nav>
            <b-nav-form v-on:submit.prevent="">
              <b-form-input size="sm" class="mr-sm-2" ref="searchQuery" v-on:keyup.enter="search" placeholder="Search"></b-form-input>
              <b-button size="sm" class="my-2 my-sm-0 bg-info" type="button" @click="search">Search</b-button>
            </b-nav-form>

            <b-nav-item-dropdown right>
              <!-- Using 'button-content' slot -->
              <template v-slot:button-content>
                <em>{{ $root.UserName }}</em>
              </template>
              <b-dropdown-item href="#/Profile">Profile</b-dropdown-item>
              <b-dropdown-item @click="logout">Sign Out</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-navbar-nav>

      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  methods: {
    logout() {
      var self = this;
      axios.post(this.$root.backendUrl + '/User/Logout', {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.$cookies.remove("token");
          self.$cookies.remove("userdata");
		  window.location.href = "/";
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    search() {
      var query = this.$refs.searchQuery.$data.localValue;
      console.log(query)
      if (query != "") {
	    window.location.href = "#/Search?query=" + query;
		window.location.reload();
      }
    }
  },
  computed: {
    IsUser() { return this.$root.UserRole == "User"; },
	IsAdmin() {  return this.$root.UserRole == "Admin"; }
  }
}
</script>
