<template>
  <div class="container col-4">
    <form class="row myform justify-content-center">
      <h1 class="h3 mb-3 font-weight-normal">Sign up</h1>
      <hr class="line">
      <label v-show="alertMessage != ''" style="color: red;">{{ alertMessage }}</label>
      <div class="inputFields">
        <label for="Email" class="sr-only">E-mail adress</label>
        <input type="email"
               id="Email"
               class="form-control mb-1 signTextField"
               placeholder="E-mail address"
               v-model="email"
               required
               autofocus />
        <label for="Password" class="signLabel sr-only">Password</label>
        <input type="password"
               id="Password"
               class="form-control mb-1 signTextField"
               placeholder="Password"
               v-model="password"
               required />
        <label for="ConfirmPassword" class="signLabel sr-only">Confirm password</label>
        <input type="password"
               id="ConfirmPassword"
               class="form-control mb-1 signTextField"
               placeholder="Confirm password"
               v-model="confirmPassword"
               required />
        <br>
        <button class="btn btn-lg btn-secondary mt-2 mb-2"
                type="button"
                @click="register">
          Sign up
        </button>
      </div>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'
  import GoogleLogin from 'vue-google-login';

export default {
  name: 'SignUp',
  components: {
    GoogleLogin
  },
  data() {
    return {
      email: "",
      password: "",
      confirmPassword: "",
      alertMessage: ""
    }
  },
  methods: {
    register() {
      var self = this;
      console.log('register')
      if (this.password === this.confirmPassword) {
        axios.put(this.$root.backendUrl + '/User/SignUp', {
          Email: this.email,
          Password: this.password
        })
          .then(function (response) {
            if (response.data.success) {
              self.$cookies.set("token", response.data.message)
              window.location.href = "#/Home";
			  window.location.reload();
            } else {
              self.alertMessage = response.data.message;
            }
          }).catch(function (error) {
            console.log(error)
          })
      } else {
        self.alertMessage = "The Password and ConfirmPassword are not matching!";
      }
    }
  }
}
</script>
