import Vue from 'vue'
import App from './App.vue'
import VueCookies from 'vue-cookies';
import { BootstrapVue, IconsPlugin, BootstrapVueIcons } from 'bootstrap-vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret, faSpinner } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue-icons.min.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import { LoaderPlugin } from 'vue-google-login';
import msal from 'vue-msal'
import router from './router'
import axios from 'axios'

library.add(faUserSecret, faSpinner)

Vue.config.productionTip = false
Vue.use(LoaderPlugin, {
  client_id: "2336917677-n7ranvig6cuooa4ttb39e97ptjo4kn2e.apps.googleusercontent.com"
});
Vue.use(msal, {
  auth: {
    clientId: '72ee3213-87e2-4324-891d-abf105703dd6'
  }
});
Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)
Vue.use(IconsPlugin)
Vue.use(VueCookies);
Vue.component('font-awesome-icon', FontAwesomeIcon)

new Vue({
  router,
  render: h => h(App),
  data() {
    return {
      backendUrl: "https://watchsushi.ddns.net/api",
      UserRole: "",
      UserName: "",
      Name: "",
      NickName: ""
    }
  },
  async created() {
    var user = this.$cookies.get("userdata");
    if (user !== null) {
      this.UserName = user.UserName
      this.UserRole = user.UserRole
      this.NickName = user.NickName
      this.Name = user.Name
    } else {
      try {
        var response = await axios.get(this.$root.backendUrl + "/User", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') } });
        this.UserRole = response.data.userRole
        this.UserName = response.data.userName
        if (response.data.nickName != "") {
          this.NickName = response.data.nickName
        } else {
          this.NickName = "Anonymous"
        }
        this.Name = response.data.name
        var userData = {
          UserName: this.UserName,
          Name: this.Name,
          NickName: this.NickName,
          UserRole: this.UserRole
        };
        this.$cookies.set("userdata", userData);
      } catch (e) {
        console.log(e);
      }
    }
  }
}).$mount('#app')
