<template>
  <div class="container col-4">
    <form class="row myform justify-content-center">
      <h1 class="h3 mb-3 font-weight-normal">Sign in</h1>
      <div class="icons">
        <GoogleLogin style="padding-left: 110px;" :params="googleParams" :renderParams="renderGoogleParams" :onSuccess="onGoogleSuccess" :onFailure="onGoogleFailure"></GoogleLogin>
        <div style="padding-left: 110px;" @click="microsoftSignin">
          <div style="height:41px;width:215px;" class="abcRioButton abcRioButtonLightBlue">
            <div class="abcRioButtonContentWrapper" style="width: 215px; height: 41px;">
              <div class="abcRioButtonIcon" style="padding:11px">
                <div style="width:18px;height:18px;" class="abcRioButtonSvgImageWithFallback abcRioButtonIconImage abcRioButtonIconImage18">
                  <img src="https://docs.microsoft.com/en-us/azure/active-directory/develop/media/howto-add-branding-in-azure-ad-apps/ms-symbollockup_mssymbol_19.svg" width="18px" height="18px"/>
                </div>
              </div>
              <span style="font-size:14px;line-height:39px;" class="abcRioButtonContents">
                <span id="not_signed_innj6bex8sovzo" style="display: none;">Sign in with Microsoft</span>
                <span id="connectednj6bex8sovzo" style="">Sign in with Microsoft</span>
              </span>
            </div>
          </div>
        </div>
      </div>
        <hr class="line">
        <label v-show="alertMessage != ''" style="color: red;">{{ alertMessage }}</label>
        <div class="inputFields">
          <label for="Email" class="signLabel sr-only">E-mail adress</label>
          <input type="email"
                 id="Email"
                 class="form-control mb-1 signTextField"
                 placeholder="E-mail address"
                 v-model="email"
                 required
                 autofocus />
          <label for="Password" class="signLabel sr-only">Password</label>
          <input type="password"
                 id="Password"
                 v-model="password"
                 class="form-control mb-1 signTextField"
                 placeholder="Password"
                 required />
          Remember me
          <input Class="remeberMe" type="checkbox" id="KeepMeSignIn" v-model="remember" checked />
          <br>
          <button class="btn btn-lg btn-secondary mt-2 mb-2"
                  type="button"
                  @click="login">
            Sign in
          </button>
          <br>
          <label class="signLabel">
            <a class="link pt-2" href="#/ForgotPassword">Forgot your password?</a>
          </label>
          <br>
          <label class="signLabel">
            Not a member yet?
            <a class="link pt-2" href="#/Signup">Sign up!</a>
          </label>
        </div>
</form>
  </div>
</template>

<script>
  import axios from 'axios'
  import GoogleLogin from 'vue-google-login';

export default {
    name: 'SignIn',
    components: {
       GoogleLogin
    },
    data() {
      return {
        email: "",
        password: "",
        remember: true,
        alertMessage: "",
        googleParams: {
          client_id: "2336917677-n7ranvig6cuooa4ttb39e97ptjo4kn2e.apps.googleusercontent.com"
        },
        renderGoogleParams: {
            width: 215,
            height: 41,
            longtitle: true
        }
      }
    },
    methods: {
      login() {
        var self = this;
        var jsonData = {
          Email: this.email.trim(),
          Password: this.password.trim(),
          KeepMeSignedIn: this.remember
        };

        axios.post(this.$root.backendUrl + '/User/Login', jsonData)
          .then(function (response) {
            if (response.data.success) {
              self.$cookies.set('token', response.data.message);
              window.location.href = "#/Home";
			  window.location.reload();
            } else {
              self.alertMessage = response.data.message;
            }
          }).catch(function (error) {
            console.log(error)
            self.alertMessage = error;
          })
      },
      onGoogleSuccess(googleUser) {
        var self = this;
        console.log(googleUser);
        console.log(googleUser.getBasicProfile());
        axios.get(this.$root.backendUrl + '/User/ExternalLoginResponse?access_token=' + googleUser.tc.access_token + "&access_type=" + googleUser.tc.idpId)
      .then(function (response) {
          self.$cookies.set('token', response.data);
		  window.location.href = "#/Home";
		  window.location.reload();
      }).catch(function (error) {
        console.log(error)
        self.alertMessage = error;
      })
    },
    onGoogleFailure(error) {
      console.log(error)
    },
    microsoftSignin() {
      var self = this;
      console.log('microsoft register')
      this.$msal.signIn();
	  console.log(this.$msal);
      axios.get(this.$root.backendUrl + '/User/ExternalLoginResponse?access_token=' + this.$msal.data.accessToken + "&access_type=microsoft")
      .then(function (response) {
          self.$cookies.set('token', response.data);
          window.location.href = "#/Home";
		  window.location.reload();
      }).catch(function (error) {
        console.log(error)
        self.alertMessage = error;
      })
    }
  }
}
</script>

