<template>
  <div class="container">
    <div class="row">
      <ItemList ref="searchList"></ItemList>
    </div>
  </div>
</template>

<script>
  import ItemList from '../components/ItemList.vue'
  import axios from 'axios'

  export default {
    data() {
      return {
        filmitems: [],
        seriesitems: []
      }
    },
    components: {
      ItemList
    },
    async mounted() {
      console.log(this.$root.UserRole)
      console.log(this.$root.UserName)
      var self = this
      this.$refs.searchList.Title = "Search results:"
      axios.get(this.$root.backendUrl + "/Films/Search?query=" + this.$route.query.query, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.filmitems = response.data;
          self.$refs.searchList.items = self.filmitems.concat(self.seriesitems);
        })
        .catch(function (error) {
          console.log(error)
        })
      axios.get(this.$root.backendUrl + "/Series/Search?query=" + this.$route.query.query, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.seriesitems = response.data;
          self.$refs.searchList.items = self.seriesitems.concat(self.filmitems);
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
</script>
