<template>
  <div class="home">
    <img class="logo" alt="WatchSushi logo" src="../assets/watchsushi-transp.jpg" height="250" width="250">
    <h1>WatchSushi</h1>
    <h4>Track your favorite TV shows and Films in one place!</h4>
    <hr>
    <div class="offer">
      <h2>What WatchSushi Offers</h2>
      <div align="left">
        <h5><span class="fas fa-check-square fa-2x p-1 m-1 text-center"></span> <b>TRACK</b> shows and movies you love</h5>
        <h5><span class="fas fa-pencil-alt fa-2x p-1 m-1 text-center"></span> <b>LEAVE</b> your mark, write a comment</h5>
        <h5><span class="fas fa-heart fa-2x p-1 m-1 text-center"></span> <b>SAVE</b> your favorites</h5>
        <h5><span class="fas fa-map fa-2x p-1 m-1 text-center"></span> <b>DISCOVER</b> what to watch next</h5>
      </div>
    </div>
    <hr>
    <div class="editor" align="left">
      <h2>Editor's pick:</h2>
      <div>
        <div v-for="(item, itemIndex) in picks" class="col-3" style="display: inline-block" :key="itemIndex">
          <img :src="getImgUrl(item.image)" width="130" height="200">
          <span>{{item.title}}</span>
        </div>
      </div>
    </div>
    <hr>
    <div>
      <div class="signup" align="right">
        <h5>Start today. Be part of our community!</h5>
        <button class="btn btn-lg btn-secondary mt-2 mb-2"
                type="button"
                onclick="window.location='/Signup'">
          Sign up
        </button>
      </div>
      <div class="signin" align="left">
        <h5>Already a member? Keep tracking!</h5>
        <button class="btn btn-lg btn-secondary mt-2 mb-2"
                type="button"
                onclick="window.location='/Signin'">
          Sign in
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"

export default {
  data () {
    return {
      picks: []
    }
  },
  methods: {
    getLink(item) {
      if (item.filmID === undefined) {
        return '/Series/' + item.seriesID;
      } else {
        return '/Films/' + item.filmID;
      }
    },
    getImgUrl(imageStr) {
      var image = null
      if (imageStr == null) {
        var images = require.context('../assets/', false, /\.png$/)
        image = images('./placeholder.png')
        return image
      }
      image = 'data:image/png;base64,' + imageStr;
      return image
    }
  },
  async mounted() {
    // Get recommendations
    this.picks = []
    var self = this;
    axios.get(this.$root.backendUrl + "/Series/Recommended")
      .then(function (response) {
        for (var i = 0; i < response.data.length; i++) {
          self.picks.push(response.data[i]);
        }
        })
        .catch(function (error) {
          console.log(error);
        })
     axios.get(this.$root.backendUrl + "/Films/Recommended")
       .then(function (response) {
         for (var i = 0; i < response.data.length; i++) {
           self.picks.push(response.data[i]);
         }
       })
       .catch(function (error) {
         console.log(error);
       })
  }
}
</script>

