<template>
  <div class="row detailsHeader b-flex flex-row" align="left">
    <div class="headerImage container d-flex h-100 col-md-2 col-6">
      <div class="row align-items-center h-100 mx-auto">
        <img :src="getImgUrl(Image)" width="130" height="200">
      </div>
    </div>
    <div class="headerDescription col-md-9 col-6">
      <div>
        <div class="headerButtons">
          <a @click="favoriteClick()" v-if="!this.$route.path.includes('Episode')" class="far fa-heart p-1 m-1 text-center favoriteButton"
             ref="favoriteButton"
             lang="no">
          </a>
          <a @click="recommendButton()" v-if="this.$root.UserRole == 'Admin' && !this.$route.path.includes('Episode')" class="fas fa-bullhorn p-1 m-1 text-center recommendButton"
             ref="recommendButton"
             lang="no">
          </a>
          <a @click="saveButton()" v-if="!this.$route.path.includes('Episode')" class="far fa-save p-1 m-1 text-center saveButton"
             ref="saveButton"
             lang="no">
          </a>
          <a @click="watchedButton" class="fas fa-eye p-1 m-1 text-center watchedButton"
             ref="watchedButton"
             lang="no">
          </a>
        </div>
        <div>
          <h5 @dblclick="changeTitle" v-show="!edit.title">{{ title }}</h5>
          <input type="text" ref="titleText" v-show="edit.title" @keyup.enter="changeTitleText" :value="title"></input>
        </div>
      </div>
      <div>
        <p @dblclick="changeDescription" v-show="!edit.description">
          {{ description }}
        </p>
        <textarea ref="descriptionText" v-show="edit.description" @keyup.enter="changeDescriptionText">{{ description }}</textarea>
      </div>
    </div>
  </div>
</template>


<script>
  import axios from 'axios'

export default {
  name: 'ItemList',
  props: {
    Title: {
      type: String,
      default: 'Unknown'
    },
    Description: {
      type: String,
      default: 'Unknown'
    },
    Image: {
      type: String,
      default: 'placeholder'
    }
    },
    data() {
      return {
        edit: {
          title: false,
          description: false
        },
        descriptionText: "",
        titleText: ""
      }
    },
    computed: {
      title() {
        if (this.titleText == "") {
          this.titleText = this.Title;
        }
        return this.titleText;
      },
      description() {
        if (this.descriptionText == "") {
          this.descriptionText = this.Description;
        }
        return this.descriptionText;
      }
    },
    watch: {
      Title: function (value) {
        this.titleText = value;
      },
      Description: function (value) {
        this.descriptionText = value;
      },
      Image: function (value) {
        this.Image = value;
      }
    },
    async created() {
      var self = this;
      axios.get(this.$root.backendUrl + this.$route.path + "/Favorite", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.$refs.favoriteButton.lang = response.data ? 'yes' : 'no';
        })
        .catch(function (error) {
          console.log(error)
        });
      axios.get(this.$root.backendUrl + this.$route.path + "/Save", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.$refs.saveButton.lang = response.data ? 'yes' : 'no';
        })
        .catch(function (error) {
          console.log(error)
        });
      axios.get(this.$root.backendUrl + this.$route.path + "/Watched", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.$refs.watchedButton.lang = response.data ? 'yes' : 'no';
        })
        .catch(function (error) {
          console.log(error)
        });
      axios.get(this.$root.backendUrl + this.$route.path + "/Recommend", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          self.$refs.recommendButton.lang = response.data ? 'yes' : 'no';
        })
        .catch(function (error) {
          console.log(error)
        });
    },
    methods: {
      getImgUrl(imageStr) {
        var image = null
        if (imageStr == null) {
          var images = require.context('../assets/', false, /\.png$/)
          console.log(images)
          image = images('./placeholder.png')
          return image
        }
        image = 'data:image/png;base64,' + imageStr;
        return image
      },
    changeTitleText() {
      if (this.$root.UserRole == 'Admin') {
        var self = this;
        axios.post(this.$root.backendUrl + this.$route.path + "/Title", {
          Changed: self.$refs.titleText.value
        }, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
            .then(function (response) {
              self.titleText = response.data.title;
              self.edit.title = !self.edit.title;
            })
            .catch(function (error) {
              alert(error)
            });
      }
    },
    changeDescriptionText() {
      if (this.$root.UserRole == 'Admin') {
        var self = this;
        axios.post(this.$root.backendUrl + this.$route.path + "/Description", {
          Changed: self.$refs.descriptionText.value
        }, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
            .then(function (response) {
              self.descriptionText = response.data.description;
              self.edit.description = !self.edit.description;
            })
            .catch(function (error) {
              alert(error)
            });
      }
    },
    changeTitle() {
      if (this.$root.UserRole == 'Admin') {
        this.edit.title = !this.edit.title;
      }
    },
    changeDescription() {
      if (this.$root.UserRole == 'Admin') {
        this.edit.description = !this.edit.description;
      }
    },
    favoriteClick() {
      var self = this;
      axios.post(this.$root.backendUrl + this.$route.path + "/Favorite", {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          var clicked = self.$refs.favoriteButton.lang;
          if (clicked == "no") {
            self.$refs.favoriteButton.lang = "yes"
          } else {
            self.$refs.favoriteButton.lang = "no"
          }
        })
        .catch(function (error) {
          alert(error)
        });
    },
    recommendButton() {
      var self = this;
      axios.post(this.$root.backendUrl + this.$route.path + "/Recommend", {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          var clicked = self.$refs.recommendButton.lang;
          if (clicked == "no") {
            self.$refs.recommendButton.lang = "yes"
          } else {
            self.$refs.recommendButton.lang = "no"
          }
        })
        .catch(function (error) {
          alert(error)
        });
    },
    saveButton() {
      var self = this;
      axios.post(this.$root.backendUrl + this.$route.path + "/Save", {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          var clicked = self.$refs.saveButton.lang;
          if (clicked == "no") {
            self.$refs.saveButton.lang = "yes"
          } else {
            self.$refs.saveButton.lang = "no"
          }
        });
    },
    watchedButton() {
      var self = this;
      axios.post(this.$root.backendUrl + this.$route.path + "/Watched", {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
        .then(function (response) {
          var clicked = self.$refs.watchedButton.lang;
          if (clicked == "no") {
            self.$refs.watchedButton.lang = "yes"
          } else {
            self.$refs.watchedButton.lang = "no"
          }
        });
    }
  }
 }
</script>

<style>
  .detailsHeader{
    margin-right: 0;
    margin-left: 0;
    margin-top: 15px;
    width: 100%;
    background-color: #eee;
  }
  .headerImage{
    float: left;
    min-width: 210px;
    padding-left: 50px;
    padding: 30px;
    vertical-align: middle;
  }
  .headerDescription{
    float: right;
    padding: 20px;
  }
  .headerButtons {
    float: right;
    display: inline;
  }
  a:lang(no) {
    color: black;
  }
  a:lang(yes) {
    color: blue;
  }
</style>
