<template>
  <div class="moreInformation">
    <h3>More information</h3>
    <div class="moreInformationBox">
      <p>Release date: <span @dblclick="changeReleaseDate" v-show="!edit.releaseDate">{{ releaseDate }}</span>
      <input type="text" ref="releaseDateText" v-show="edit.releaseDate" @keyup.enter="changeReleaseDateText" :value="releaseDate"></input>
      </p>
      <p v-show="!this.$route.path.includes('Episode')">
        Genre: <span @dblclick="changeGenre" v-show="!edit.genre">{{ genre }}</span>
        <input type="text" ref="genreText" v-show="edit.genre" @keyup.enter="changeGenreText" :value="genre"></input>
      </p>
      <p v-show="!this.$route.path.includes('Episode')">
        Director: <span @dblclick="changeDirector" v-show="!edit.director">{{ director }}</span>
        <input type="text" ref="directorText" v-show="edit.director" @keyup.enter="changeDirectorText" :value="director"></input>
      </p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    props: {
      ReleaseDate: {
        type: String,
        default: '0000.00.00'
      },
      Genre: {
        type: String,
        default: 'Unknown'
      },
      Director: {
        type: String,
        default: 'Unknown'
      }
    },
    data() {
      return {
        edit: {
          releaseDate: false,
          genre: false,
          director: false
        },
        directorText: "",
        genreText: "",
        releaseDateText: ""
      }
    },
    watch: {
      ReleaseDate: function (value) { this.releaseDateText = value; },
      Genre: function (value) { this.genreText = value; },
      Director: function (value) { this.directorText = value; },
    },
    computed: {
      director() {
        if (this.directorText == "") {
          this.directorText = this.Director;
        }
        return this.directorText;
      },
      genre() {
        if (this.genreText == "") {
          this.genreText = this.Genre;
        }
        return this.genreText;
      },
      releaseDate() {
        if (this.releaseDateText == "") {
          this.releaseDateText = this.ReleaseDate;
        }
        return this.releaseDateText;
      }
    },
    methods: {
      changeReleaseDate() {
        if (this.$root.UserRole == 'Admin') {
          this.edit.releaseDate = !this.edit.releaseDate;
        }
      },
      changeGenre() {
        if (this.$root.UserRole == 'Admin') {
          this.edit.genre = !this.edit.genre;
        }
      },
      changeDirector() {
        if (this.$root.UserRole == 'Admin') {
          this.edit.director = !this.edit.director;
        }
      },
      changeReleaseDateText() {
        if (this.$root.UserRole == 'Admin') {
          var self = this;
          axios.post(this.$root.backendUrl + this.$route.path + "/ReleaseDate", {
            Changed: self.$refs.releaseDateText.value
          }, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
            .then(function (response) {
              self.releaseDateText = response.data.createDate;
              self.edit.releaseDate = !self.edit.releaseDate;
            })
            .catch(function (error) {
              alert(error)
            });
        }
      },
      changeGenreText() {
        if (this.$root.UserRole == 'Admin')
          var self = this;
          axios.post(this.$root.backendUrl + this.$route.path + "/Category", {
            Changed: Self.$refs.genreText.value
          }, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
            .then(function (response) {
              self.genreText = response.data.category;
              self.edit.genre = !self.edit.genre;
            })
            .catch(function (error) {
              alert(error)
            });
        }
      },
      changeDirectorText() {
        if (this.$root.UserRole == 'Admin') {
          var self = this;
          axios.post(this.$root.backendUrl + this.$route.path + "/Director", {
            Changed: self.$refs.directorText.value
          }, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
            .then(function (response) {
              self.directorText = response.data.director;
              self.edit.director = !self.edit.director;
            })
            .catch(function (error) {
              alert(error)
            });
        }
      }
    }
</script>

<style>
  .moreInformation {
    margin-top: 15px;
    width: 100%;
    text-align: left;
  }
  .moreInformationBox{
    width: 100%;
    background-color: #eee;
    padding: 20px;
  }
</style>
