<template>
  <div class="container">
    <div class="row">
      <DetailsHeader :Title="seriesTitle" :Image="seriesImage" :Description="seriesDescription"></DetailsHeader>
      <MoreInformation :ReleaseDate="seriesReleaseDate" :Genre="seriesGenre" :Director="seriesDirector"></MoreInformation>
        <SeasonList :seasons="seriesSeasons"></SeasonList>
      <CommentSection :comments="seriesComments"></CommentSection>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import DetailsHeader from '../components/DetailsHeader.vue'
  import MoreInformation from '../components/MoreInformation.vue'
  import CommentSection from '../components/CommentSection.vue'
  import SeasonList from '../components/SeasonList.vue'

  export default {
    data() {
      return {
        seriesTitle: "Unknown",
        seriesImage: null,
        seriesDescription: "Unknown",
        seriesReleaseDate: "0000.00.00",
        seriesGenre: "Unknown",
        seriesDirector: "Unknown",
        seriesComments: [],
        seriesSeasons: []
      }
    },
    components: {
      DetailsHeader,
      MoreInformation,
      SeasonList,
      CommentSection
    },
    async created() {
      var self = this;
      axios.get(this.$root.backendUrl + this.$route.path)
        .then(function (response) {
          self.seriesTitle = response.data.title;
          self.seriesDescription = response.data.description;
          self.seriesImage = response.data.image;
          self.seriesReleaseDate = response.data.createDate;
          self.seriesGenre = response.data.category;
          self.seriesDirector = response.data.director;
          self.seriesComments = response.data.comments
          self.seriesSeasons = response.data.seasons;
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
</script>
