<template>
  <div class="container col-4">
    <form class="row myform justify-content-center">
      <h1 class="h3 mb-3 font-weight-normal">Profile</h1>
      <hr class="line">
      <label v-show="alertMessage != ''" style="color: red;">{{ alertMessage }}</label>
      <div class="ProfileFields">
        <label class="ProfileLabel" for="Name">Name</label>
        <input id="Name"
               class="form-control mb-1"
               :placeholder="FullName"
               v-model="FullName" />
        <label class="ProfileLabel" for="Email">E-mail adress</label>
        <input type="email"
               id="Email"
               class="form-control mb-1"
               :placeholder="emailAddress"
               v-model="emailAddress"
               disabled />
        <label class="ProfileLabel" for="NickName">Nickname</label>
        <input id="NickName"
               class="form-control mb-1"
               :placeholder="EditNickname"
               v-model="EditNickName" />
        <label class="ProfileLabel" for="CurrentPassword">Current password</label>
        <input type="password"
               id="CurrentPassword"
               placeholder="Current password"
               v-model="CurrentPassword"
               class="form-control mb-1" />
        <label class="ProfileLabel" for="NewPassword">New password</label>
        <input type="password"
               id="NewPassword"
               placeholder="New password"
               v-model="NewPassword"
               class="form-control mb-1" />
        <label class="ProfileLabel" for="ConfNewPassword">Confirm new password</label>
        <input type="password"
               id="ConfNewPassword"
               placeholder="Confirm new password"
               v-model="ConfirmPassword"
               class="form-control mb-1" />
        <div class="ButtonFields">
          <button class="btn btn-lg btn-secondary mt-2 mb-2"
                  type="button"
                  @click="saveProfileMod">
            Save
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    data() {
      return {
        FullName: this.$root.Name,
        emailAddress: this.$root.UserName,
        EditNickName: this.$root.NickName,
        NewPassword: '',
        CurrentPassword: '',
        ConfirmPassword: '',
        alertMessage: ''
      }
    },
    async mounted() {
      var self = this
      if (this.$root.UserRole != '') {
        axios.get(this.$root.backendUrl + "/User", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            self.FullName = response.data.name
            self.emailAddress = response.data.userName
            self.EditNickname = response.data.nickName
          })
          .catch(function (error) {
            console.log(error);
          })
      }
    },
    methods: {
      saveProfileMod() {
        var self = this;
        if (this.NewPassword === this.ConfirmPassword || this.NewPassword == "") {
        axios.post(this.$root.backendUrl + "/User", {
          Name: this.FullName,
          Email: this.emailAddress,
          NickName: this.EditNickName,
          CurrentPassword: this.CurrentPassword,
          NewPassword: this.NewPassword
        }, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            self.$root.UserName = self.emailAddress
            self.$root.NickName = self.EditNickName
            self.$root.Name = self.FullName
            var cacheProfile = self.$cookies.get("userdata")
            cacheProfile.NickName = self.EditNickName
            cacheProfile.UserName = self.emailAddress
            cacheProfile.Name = self.FullName
            self.$cookies.set("userdata", cacheProfile)
            self.$router.push('/Home')
            self.$router.go()
          })
          .catch(function (error) {
            console.log(error);
          });
        } else {
        self.alertMessage = "The Password and ConfirmPassword are not matching!";
      }
      }
    }
  }
</script>

<style>
  .ProfileFields {
    text-align: left;
    width: 100%;
  }
  .ButtonFields {
    text-align: right;
  }
  .ProfileLabel {
    margin-top: 15px;
  }
</style>
