<template>
  <div class="seasonList">
    <div v-for="(season, seasonIndex) in Seasons">
      <div class="seasonBox row">
        <div class="col-sm-6 seasonName">
          <a v-b-toggle="'collapseSeason' + seasonIndex" variant="primary">{{ season.title }}</a>
        </div>
        <div class="col-sm-6 seasonListButton">
          <a @click="watchedSeasonButton(season.seasonID)" class="fas fa-eye p-1 m-1 text-center watchedSeasonButton"
             :ref="season.seasonID" :lang="season.watched">
          </a>
        </div>
      </div>
      <b-collapse :id="'collapseSeason' + seasonIndex">
        <div  v-for="(episode, index) in sortEpisodes(season.episodes)" class="episodeBox row">
          <div class="col-sm-6 seasonName">
            <a class="episodeLink" :href="'#/Episodes/' + episode.episodeID">{{ episode.index }}</a>
          </div>
          <div class="col-sm-6 seasonListButton">
            <a @click="watchedEpisodeButton(episode)" class="fas fa-eye p-1 m-1 text-center watchedEpisodeButton"
               :lang="episode.watched"
               :ref="episode.episodeID">
            </a>
          </div>
        </div>
      </b-collapse>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    data() {
      return {
        localSeasons: []
      }
    },
    props: {
      seasons: {
        type: Array,
        default: [],
      }
    },
    computed: {
      Seasons() {
        if (this.localSeasons.length == 0) {
          this.localSeasons = this.seasons;
        }
        this.localSeasons.sort(function (a, b) { return a.title.localeCompare(b.title, undefined, {numeric: true}) });
        return this.localSeasons
      }
    },
    methods: {
      sortEpisodes(episodes) {
        var myEpisodes = Object.assign([], episodes)
        myEpisodes.sort(function (a, b) { return a.index.localeCompare(b.index, undefined, {numeric: true}); })
        return myEpisodes
      },
      compareSeasons(a, b) {
        return a.title.localeCompare(b.title);
      },
      watchedSeasonButton(seasonID) {
        var self = this;
        axios.post(this.$root.backendUrl + "/Seasons/" + seasonID + "/Watched", {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            var season = null;
            for (var i = 0; i < self.localSeasons.length; i++) {
              if (self.localSeasons[i].seasonID == seasonID)
                season = self.localSeasons[i];
            }
            if (season.watched == "no") {
              season.watched = 'yes'
			  self.$refs[season.seasonID][0].lang = "yes"
              for (var i = 0; i < season.episodes.length; i++) {
                season.episodes[i].watched = 'yes'
				self.$refs[season.episodes[i].episodeID][0].lang = "yes"
              }
            } else {
              season.watched = 'no'
			  self.$refs[season.seasonID][0].lang = "no"
              for (var i = 0; i < season.episodes.length; i++) {
                season.episodes[i].watched = 'no'
				self.$refs[season.episodes[i].episodeID][0].lang = "no"
              }
            }
          })
          .catch(function (error) {
            alert(error)
          });
      },
      watchedEpisodeButton(episode) {
        var self = this;
        axios.post(this.$root.backendUrl + "/Episodes/" + episode.episodeID + "/Watched", {}, { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') }})
          .then(function (response) {
            console.log(episode.episodeID)
            console.log(episode.index)
            console.log(self.$refs[episode.episodeID])
            console.log(self.$refs[episode.episodeID].lang)
            if (self.$refs[episode.episodeID][0].lang == "no") {
              self.$refs[episode.episodeID][0].lang = "yes"
              episode.watched = "yes"
          } else {
              self.$refs[episode.episodeID][0].lang = "no"
              episode.watched = "no"
          }
        })
        .catch(function (error) {
            alert(error)
        });
      }
    },
    watch: {
      seasons: {
        handler: function (newSeasons) {
          var self = this;
          var watchedEpisodes = null;
          axios.get(this.$root.backendUrl + "/User/Watched/Episodes", { headers: { Authorization: 'Bearer ' + this.$cookies.get('token') } })
            .then(function (response) {
              watchedEpisodes = response.data;
              for (var i = 0; i < newSeasons.length; i++) {
                var season = newSeasons[i];
                axios.get(self.$root.backendUrl + "/Seasons/" + season.seasonID)
                  .then(function (response) {
                    var season = null;
                    for (var i = 0; i < newSeasons.length; i++) {
                      if (newSeasons[i].seasonID == response.data.seasonID)
                        season = newSeasons[i];
                    }
                    season.episodes = response.data.episodes;
                    for (var i = 0; i < season.episodes.length; i++) {
                      if (watchedEpisodes.includes(season.episodes[i].episodeID)) {
                        season.episodes[i].watched = "yes";
                      } else {
                        season.episodes[i].watched = "no";
                      }
                    }
                    season.watched = season.episodes.every(function (v) { return v.watched == "yes"; }) ? "yes" : "no";
                  })
                  .catch(function (error) {
                    alert(error)
                  });
              }
              self.localSeasons = newSeasons;
            })
            .catch(function (error) {
              console.log(error)
            })
        }
      }
    }
  }
</script>

