<template>
  <div class="container">
    <div class="row">
      <DetailsHeader :Title="filmTitle" :Image.sync="filmImage" :Description.sync="filmDescription"></DetailsHeader>
      <MoreInformation :ReleaseDate="filmReleaseDate" :Genre="filmGenre" :Director="filmDirector"></MoreInformation>
      <CommentSection :comments="filmComments"></CommentSection>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import DetailsHeader from '../components/DetailsHeader.vue'
  import MoreInformation from '../components/MoreInformation.vue'
  import CommentSection from '../components/CommentSection.vue'

  export default {
    data() {
      return {
        filmTitle: "Unknown",
        filmImage: null,
        filmDescription: "Unknown",
        filmReleaseDate: "0000.00.00",
        filmGenre: "Unknown",
        filmDirector: "Unknown",
        filmComments: []
      }
    },
    components: {
      DetailsHeader,
      MoreInformation,
      CommentSection
    },
    async created() {
      axios.get(this.$root.backendUrl + this.$route.path)
        .then((result) => {
          this.filmTitle = result.data.title;
          this.filmDescription = result.data.description;
          this.filmImage = result.data.image;
          this.filmReleaseDate = result.data.createDate;
          this.filmGenre = result.data.category;
          this.filmDirector = result.data.director;
          this.filmComments = result.data.comments
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
</script>
