<template>
	<div class="itemList" align="left">
		<h4 class="item-list-title">{{Title}}</h4>
		<div>
			<div v-for="(item, itemIndex) in items" class="list-item-block" :key="itemIndex">
        <div class="item-block">
          <img :src="getImgUrl(item.image)" v-bind:alt="item.title" width="130" height="200">
          <br />
          <a :href="getLink(item)"><span>{{item.title}}</span></a>
        </div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'ItemList',
  data () {
    return {
      Title: "",
      items: []
    }
  },
  methods: {
    getLink(item) {
      if (item.filmID === undefined && item.episodeID === undefined) {
        return '#/Series/' + item.seriesID;
      } else if (item.seriesID === undefined && item.episodeID === undefined) {
        return '#/Films/' + item.filmID;
      } else {
        return '#/Episodes/' + item.episodeID;
      }
    },
    getImgUrl(imageStr) {
      var image = null
      if (imageStr == null) {
        var images = require.context('../assets/', false, /\.png$/)
        image = images('./placeholder.png')
        return image
      }
      image = 'data:image/png;base64,' + imageStr;
      return image
    }
  }
 }
</script>

<style> 
.list-item-block {
    display: inline-block;
    text-align: center;
    padding: 10px;
    width: 300px;
}
.item-block {
    margin-left: 15px;
    margin-right: 15px;
}
.item-list-title {
    margin-bottom: 15px;
    margin-top: 40px;
}
</style>
