<template>
  <div class="container col-4">
    <form class="row myform justify-content-center">
      <hr class="line">
      <label v-show="alertMessage != ''" style="color: red;">{{ alertMessage }}</label>
      <div class="inputFields">
        <label for="Password" class="sr-only">Password:</label>
        <input type="password"
               id="Password"
               class="form-control mb-1"
               placeholder="Password"
               v-model="password"
               required
               autofocus />
        <label for="ConfirmPassword" class="sr-only">Confirm password:</label>
        <input type="password"
               id="ConfirmPassword"
               class="form-control mb-1"
               placeholder="Confirm password"
               v-model="confirmPassword"
               required />
        <button class="btn btn-lg btn-secondary mt-2 mb-2"
                type="button"
                @click="resetPassword">
          Reset Password
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ResetPassword',
  data() {
    return {
      password: "",
      confirmPassword: "",
      alertMessage: ""
    }
  },
  methods: {
    resetPassword() {
      var self = this;
      if (this.password === this.confirmPassword) {
        axios.post(this.$root.backendUrl + '/User/ResetPassword', {
          Email: this.$route.params.email,
          Token: this.$route.params.token,
          Password: this.password,
          ConfirmPassword: this.confirmPassword
        })
          .then(function (response) {
            window.location.href = "#/Signin";
          }).catch(function (error) {
            alert("Couldn't change the password!");
            console.log(error)
          })
      } else {
        self.alertMessage = "The Password and ConfirmPassword are not matching!"
      }
    }
  }
}
</script>
