<template>
  <div class="container col-4">
    <form class="row myform justify-content-center">
      <h1 class="h3 mb-3 font-weight-normal">Forgot my password</h1>
      Please enter the email address registered on your account.
      <hr class="line">
      <div class="inputFields">
        <label for="Email" class="sr-only">E-mail adress</label>
        <input
            type="email"
            id="Email"
            class="form-control mb-1"
            placeholder="E-mail address"
            v-model="email"
            required
            autofocus
        />
        <button
            class="btn btn-lg btn-secondary mt-2 mb-2"
            type="button"
            @click="forgotPassword"
        >Forgot Password</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ResetPassword',
  data() {
    return {
      email: ""
    }
  },
  methods: {
    forgotPassword() {
      var self = this;
      axios.post(this.$root.backendUrl + '/User/ForgotPassword', {
        Email: this.email
      })
      .then(function (response) {
        alert("We sent your an e-mail!")
        window.location.href = "/";
        self.go()
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
}
</script>
