<template>
  <div class="container">
    <div class="row">
      <DetailsHeader :Title="episodeTitle" :Image="episodeImage" :Description="episodeDescription"></DetailsHeader>
      <MoreInformation :ReleaseDate="episodeReleaseDate" :Genre="episodeGenre" :Director="episodeDirector"></MoreInformation>
      <CommentSection :comments="episodeComments"></CommentSection>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import DetailsHeader from '../components/DetailsHeader.vue'
  import MoreInformation from '../components/MoreInformation.vue'
  import CommentSection from '../components/CommentSection.vue'

  export default {
    data() {
      return {
        episodeTitle: "Unknown",
        episodeImage: null,
        episodeDescription: "Unknown",
        episodeReleaseDate: "0000.00.00",
        episodeGenre: "Unknown",
        episodeDirector: "Unknown",
        episodeComments: []
      }
    },
    components: {
      DetailsHeader,
      MoreInformation,
      CommentSection
    },
    async created() {
      var self = this;
      axios.get(this.$root.backendUrl + this.$route.path)
        .then(function (response) {
          self.episodeTitle = response.data.title;
          self.episodeDescription = response.data.description;
          self.episodeImage = response.data.image;
          self.episodeReleaseDate = response.data.createDate;
          self.episodeGenre = response.data.category;
          self.episodeDirector = response.data.director;
          self.episodeComments = response.data.comments
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
</script>
